import { Component } from 'react';
import { Link } from "react-router-dom";
import SearchBar from '../../services/SearchBar';
import axios from 'axios';
import styles from '../MoviesPage/MoviesPage.module.css';

const BASE_URL = 'https://api.themoviedb.org/3/';
const KEY = '86d7387105b05ad2392fce9606e8270a';

export default class MoviesPage extends Component {
    state = {
        movies: '',
        name: ''
    }

    async componentDidMount(prevProps, prevState) {

        if (prevState.name !== this.state.name) {
            const response = await axios.get(`${BASE_URL}search/movie?api_key=${KEY}&language=en-USpage=1&include_adult=false`)
            console.log(response.data);
            //this.setState({ movies: response.data.results })
        }
    }

     formSubmitHendler = name => {
        this.setState({ name });  
    }

    render() {
        const {movies} = this.state
        return (
            <div>
                <SearchBar onSubmit={this.formSubmitHendler} />
                {movies.length > 0 && (
                    <ul>{movies.map(movie => (
                        <li className={styles.listMovie} key={movie.id}>
                            <Link to={`/movies/${movie.id}`}>
                                {movie.title}
                            </Link>
                        </li>
                    ))}
                    </ul>)}
            </div>
        )
    }
}
