import { Route, NavLink, Switch } from "react-router-dom";
import { ToastContainer } from 'react-toastify';
import HomePage from './pages/HomePage/HomePage';
import MoviesPage from './pages/MoviesPage/MoviesPage';
import MovieDetailsPage from './components/MovieDetailsPage/MovieDetailsPage';
import styles from './App.module.css';

const App = () => (
  <div className="container">
    <ul className={styles.containerLink}>
      <li><NavLink className={styles.link} to="/">Home</NavLink></li>
      <li><NavLink className={styles.link} to="/movies">Movies</NavLink></li>
    </ul>
    <Switch>
      <Route exact path="/" component={HomePage} />
      <Route exact path="/movies" component={MoviesPage} />
      <Route path="/movies/:movieId" component={MovieDetailsPage} />
    </Switch>
    <ToastContainer autoClose={3000} position="top-right" type="default"/>
  </div>

);

    
export default App;
